/* App.css */
.mainContainer {
  position: relative;
}

.segundaImagenContainer {
  scale: 255%;
  position: absolute;
  margin-top: -167.5%; 
  margin-left: 28.5%; 
  transform: translate(-50%, -50%); 
  transition: transform 0.3s ease-in-out; /* transici贸n suave */
}

.terceraImagenContainer {
  scale: 236%;
  position: absolute;
  margin-top: -168%; 
  margin-left: 57%; 
  transform: translate(-50%, -50%);
  transition: transform 0.3s ease-in-out;
}

.cuartaImagenContainer {
  scale: 98%;
  position: absolute;
  margin-top: -177%;
  margin-left: 78%;
  transform: translate(-50%, -50%);
  transition: transform 0.3s ease-in-out;
}

.quintaImagenContainer {
  scale: 315%;
  position: absolute;
  margin-top: -116%;
  margin-left: 88%;
  transform: translate(-50%, -50%);
}

.sextaImagenContainer {
  scale: 300%;
  position: absolute;
  margin-top: -90%;
  margin-left: 85%;
  transform: translate(-50%, -50%);
}

.quintaImagenContainer img {
  width: 100%;
  height: 100%;
  transition: transform 0.5s; /* Agrega transici贸n suave */
}

.sextaImagenContainer img {
  width: 100%;
  height: 100%;
  transition: transform 0.5s;
}

.segundaImagenContainer:hover {
  transform: translate(-50%, -50%) scale(1.1); /* Escala al pasar el mouse */
}

.terceraImagenContainer:hover {
  transform: translate(-50%, -50%) scale(1.1); 
}

.cuartaImagenContainer:hover {
  transform: translate(-50%, -50%) scale(1.1); 
}

.quintaImagenContainer:hover img {
  transform: scale(1.2) rotateX(15deg) rotateY(15deg); /* Escala y rotaci贸n al pasar el mouse */
}

.sextaImagenContainer:hover img {
  transform: scale(1.2) rotateX(15deg) rotateY(15deg); /* Escala y rotaci贸n al pasar el mouse */
}

.joinNowButton {
  background-color: yellow;
  color: black;
  padding: 1.5em 2em;
  font-size: 300%;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  margin-top: -12em;
  position: absolute;
  top: 5%; 
  right: 4%; 
}